body {
    font-family: 'Helvetica Neue', Arial, sans-serif; 
    background-color: #f4f4f4; 
    color: #333; 
    line-height: 1.6;
    z-index: -1;
    margin: 0;
    padding: 0;
}

.aboutContainer {   
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    margin-top: 20px;
    padding: 0;
}

.flex-item {
    background-color: rgb(48, 47, 51); 
    margin-top: 25px;
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;
    max-width: 600px;
    min-width: 360px;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    transition: transform 0.3s ease, box-shadow 0.3s ease; 
}

.flex-item:hover {
    transform: translateY(-5px); 
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); 
}

.textcontainer {
    margin-top: 0px;
    margin-left: 10px;
    margin-right: 10px;
    text-align: left;
}

h2 {
    font-size: 1.8em;
    margin-bottom: 10px;
    /* color: #bba6a6; */
    border-bottom: 2px solid #0e0e0e;
    padding-bottom: 5px;
}

h3 {
    font-size: 1.2em;
    margin-top: 15px;
    color: #eff2f4;
}

h4 {
    font-size: 0.9em;
    margin-bottom: 10px;
    color: #a5a5a5;
}

p {
    font-size: 0.9em;
    /* color: #bba6a6; */
    margin-bottom: 40px;
}

.textP {
    margin: 0px 20px 20px 20px;
}

.pictureContainer {
    margin: 10px;
}

.notHidden {
    opacity: 1;
    animation: fadeIn 2s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@media (max-width: 768px) {
    .flex-item {
        max-width: 100%;
        margin-left: 10px;
        margin-right: 10px;
    }
    
    h2 {
        font-size: 1.6em;
    }

    h3 {
        font-size: 1.3em;
    }

    p {
        font-size: 1em;
    }
}
