.header {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%);
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 60px;
  }

  h1 {
    color: rgb(255, 255, 255);
    font-size: 2rem;
    margin: 0;
    padding: 10px 20px;
  }
  .name {
    display: flex;
    padding: 0;
    margin: 0;
    padding: 10px 20px;
    align-items: center;
    justify-content: center;
  }
  
  .tabContainer {
    display: flex;
    align-items: center;
    position: relative; 
}

  .tabs {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    position: relative;
  }
  
  .tabs li {
    cursor: pointer;
    padding: 10px 20px;
    position: relative;
    
  }
  
  .tabs li.active {
    color: rgb(255, 255, 255);
  }
  
  .underline {
    position: absolute;
    bottom: 0;
    height: 2px; 
    background-color: rgb(255, 255, 255); 
    transition: left 0.3s ease;
  }
  li {
  color: rgb(255, 255, 255);
  }

@media (width < 870px) {
    h1 {
      font-size: 1.5rem;
      max-width: 90%; 
      margin: 0 auto;
    }
}
@media (width < 755px) {
  h1 {
    font-size: 1rem;
    max-width: 90%;
    margin: 0 auto;
  }
} 