.clickable-header {
    cursor: pointer;
    color: #7a98b9;
    transition: color 0.3s ease;
}

.clickable-header:hover {
    color: #eceef0; 
}

.image-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}

.box-image {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px; /* Adds spacing between images */
}

/* Responsive layout: Switch to a single column on small screens */
@media (max-width: 768px) {
    .box-image {
        grid-template-columns: 1fr; /* Single column for small screens */
    }
}

.box-image img {
    width: 100%; /* Ensure images fit within their grid space */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.picture {
    max-width: 100%;
    height: auto;
}

.picture:hover {
    transform: scale(1.05); 
}
